import Vue from 'vue'
import Router from 'vue-router'
import Smoothies from '@/components/Smoothies'
import AddSmoothie from '@/components/AddSmoothie'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Smoothies',
      component: Smoothies
    },
    {
      path: '/add-smoothie',
      name: 'AddSmoothie',
      component: AddSmoothie
    }
  ]
})



