<template>
  <div class="add-smoothie">
    <h1 class="title">Add New Smoothie Recipe</h1>
    <form @submit.prevent="addSmoothie" class="add-smoothie-form">
      <label for="name">Smoothie Name:</label>
      <input type="text" id="name" v-model="newName" />
      <div class="added-ingredients" v-for="(ing, index) in newIngArray" :key="index">
        <label for="added-ing">Ingredient:</label>
        <input type="text" id="added-ing" v-model="newIngArray[index]"/>
      </div>
      <label for="ingredient">Add an ingredient:</label>
      <input type="text" id="ingredient" v-model="newIng" @keydown.tab.prevent="pushIng" />
      <button class="add-smoothie-btn">Add Smoothie</button>
    </form>
  </div>
</template>

<script>
export default {
  name: "AddSmoothie",
  props: ["smoothies"],
  data() {
    return {
      newName: null,
      newIng: null,
      newIngArray: []
    };
  },
  methods: {
    pushIng() {
      if (this.newIng) {
        this.newIngArray.push(this.newIng);
        this.newIng = null;
      }
    },
    addSmoothie() {
      if (this.newName) {
        this.smoothies.push({ name: this.newName, ingredients: this.newIngArray})
        this.newName = null
        this.newIng = null
        this.newIngArray = []
      }
    }
  }
};
</script>

<style>
.add-smoothie {
  justify-items: center;
  margin-top: 5em;
  padding: 0 10em;
  display: flex;
  flex-direction: column;
}
.add-smoothie .title {
  color: rgb(38, 90, 187);
  font-size: 2.5em;
  text-align: center;
}
.add-smoothie .add-smoothie-form {
  display: flex;
  flex-direction: column;
  width: 600px;
  margin: 3em auto 0;
}
.add-smoothie-form label {
  color: #777;
  margin-bottom: 1em;
}
.add-smoothie-form input {
  border: none;
  border-bottom: 1px solid #444;
  margin-bottom: 3em;
}
.add-smoothie .added-ingredients {
    display: flex;
    flex-direction: column;
}
.add-smoothie .add-smoothie-btn {
  align-self: center;
  background-color: rgb(238, 44, 83);
  color: #fff;
  text-transform: uppercase;
  border: none;
  box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.75);
  border-radius: 5px;
  padding: 1em 2em;
  cursor: pointer;
  letter-spacing: 1px;
}
</style>



