<template>
  <div class="app">
    <div class="navigation">
      <a class="smoothies-btn" @click="toggle1">Smoothie Maker App</a>
      <a class="add-smoothie-btn" @click="toggle2">+</a>
    </div>
    <Smoothies v-if="!showAddSmoothie" :smoothies="smoothies"/>
    <AddSmoothie v-if="showAddSmoothie" :smoothies="smoothies"/>
  </div>
</template>

<script>
import Smoothies from '@/components/Smoothies'
import AddSmoothie from '@/components/AddSmoothie'

export default {
  name: "App",
  components: {
    Smoothies,
    AddSmoothie
  },
  data() {
    return {
      showAddSmoothie: false,
      smoothies: [
        {
          name: "Solid Chocolate",
          ingredients: ["chocolate", "banana", "cinnamon", "shaved ice"]
        },
        {
          name: "Strawberry Shortcake",
          ingredients: ["strawberry", "pound cake", "whipped cream"]
        },
        {
          name: "Kiwi Madness",
          ingredients: ["kiwi", "strawberry", "shaved ice"]
        }
      ]
    }
  },
  methods: {
    toggle1() {
      this.showAddSmoothie = false
    },
    toggle2() {
      this.showAddSmoothie = true
    }
  }
};
</script>

<style>
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.navigation {
  background-color: rgb(38, 90, 187);
  width: 100vw;
  height: 140px;
  position: relative;
}
.navigation .smoothies-btn {
  color: #fff;
  text-decoration: none;
  font-size: 3.5em;
  position: absolute;
  left: 1em;
  top: 0.5em;
  cursor: pointer;
}
.navigation .add-smoothie-btn {
  position: absolute;
  right: 2em;
  bottom: -.8em;
  color: #fff;
  text-decoration: none;
  background-color: rgb(238, 44, 83);
  width: 80px;
  height: 80px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3em;
  font-weight: bold;
  cursor: pointer;
}
</style>



