<template>
  <div class="smoothies">
    <div class="smoothie" v-for="(smoothie, index) in smoothies" :key="index">
      <h1 class="smoothie-name">{{smoothie.name}}</h1>
      <i class="fas fa-trash trash" @click="deleteSmoothie(index)"></i>
      <div class="ingredients">
        <span v-for="(ing, index) in smoothie.ingredients" :key="index" class="smoothie-ing">{{ing}}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Smoothies",
  props: ["smoothies"],
  data() {
    return {
      
    }
  },
  methods: {
    deleteSmoothie(index) {
      this.smoothies.splice(index, 1)
    }
  }
};
</script>

<style>
.smoothies {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-items: center;
  margin-top: 3.5em;
  padding: 0 10em;
}
.smoothie {
  width: 350px;
  height: 300px;
  box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.75);
  padding: 2em;
  position: relative;
  margin-bottom: 3.5em;
}
.smoothie .trash {
  position: absolute;
  top: 0.8em;
  right: 0.8em;
  opacity: 0.5;
  font-size: 1.5em;
  cursor: pointer;
}
.smoothie .smoothie-name {
  text-align: center;
  color: #265abb;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.smoothie .ingredients {
  display: flex;
  flex-wrap: wrap;
}
.smoothie .smoothie-ing {
  color: #333;
  background-color: #ddd;
  border-radius: 50px;
  margin: .2em;
  padding: .5em;
}
</style>



